a {
  text-decoration: none;
  color: #333;
  -webkit-transition: color .2s;
  transition: color .2s;
  cursor: pointer; }

a:hover {
  text-decoration: underline;
  color: #09c762; }

a:focus, area:focus {
  outline: 0; }

::-moz-selection {
  background: #09c762;
  color: #fff; }

::selection {
  background: #09c762;
  color: #fff; }

canvas {
  -ms-touch-action: double-tap-zoom; }

.fs14 {
  font-size: 14px; }

.red, a.red, a.red:hover, .pink, a.pink, a.pink:hover {
  color: #09c762; }

.gray999, .gray, a.gray, a.gray:hover {
  color: #999; }

.green {
  color: #2b9b2d; }

.blue, .blue:hover {
  color: #09f; }

#wrapper {
  width: 1196px;
  margin: 10px auto 80px; }
